.btn {
    font-size: 13.5px;
    font-weight: 500;
    line-height: 1.54;
    position: relative;
    display: inline-flex;
    overflow: hidden;
    align-items: center;
    justify-content: center;
    max-width: 440px;
    min-height: 55px;
    margin: 0;
    // padding: 17px 50px;
    // padding-right: 130px;
    padding: 1em 2em;
    cursor: pointer;
    user-select: none;
    transition: background-color $default-transition-settings;
    text-align: left;
    text-decoration: none;
    text-transform: uppercase;
    color: $dark-blue;
    border: 0;
    background-color: $color-main;
    background-image: none;
    // white-space: nowrap;
    // text-overflow: ellipsis;
    @include viewport--xs {
        width: 100%;
        min-width: auto;
        max-width: 100%;
        padding-right: 88px;
        padding-left: 10px;
        text-align: center;
    }
    &:hover,
    &:focus {
        text-decoration: none;
        color: saturate($dark-blue, 50);
        background-color: $color-main-light;
        span {
            background-color: $color-main;
        }
        svg {
            fill: $color-main-light;
        }
    }
    &--disabled,
    &:disabled {
        // opacity: $opacity;
        cursor: not-allowed;
        // color: $text-color;
        // background-color: $gray-lightest;
        // border-color: $gray-lighter;
    }
    span {
        position: absolute;
        top: 0;
        right: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 64px;
        height: 100%;
        transition: background-color $default-transition-settings;
        background-color: $color-main-light;
    }
    svg {
        position: relative;
        width: 12px;
        height: 10px;
        transition: fill $default-transition-settings;
        fill: $color-main-dark;
    }
}

.btn--blue {
    max-width: 390px;
    background-color: $dark-blue;
    color: $white;
    span {
        background-color: $dark-blue-saturate;
    }
    &:hover,
    &:focus {
        background-color: darken($dark-blue, 20);
        color: darken($white, 23);
        span {
            background-color: darken($dark-blue-saturate, 20);
        }
    }
}

