.interior {
  background: url("../../img/bg/interior-bg.jpg") no-repeat right top;
  background-size: contain;
  padding-left: cvw(160px);

  @include viewport--lg {
    padding-left: 40px;
    background-size: 50%;
    background-position: right bottom 40%;
  }

  @include viewport--md {
    background-size: contain;
    background-position: right cvwt(-350px) bottom 40%;
  }

  @include viewport--sm {
    // background-position: right cvwt(-650px) top;
    background: none;
    padding: 0 15px;
  }

  &-headline {
    max-width: 40vw;
    font-size: cvw(40px);
    font-weight: bold;
    line-height: 1.06;
    letter-spacing: 1.32px;
    color: $dark-blue;
    margin: cvw(230px) 0 cvw(51px);

    @include viewport--lg {
      max-width: 49vw;
      margin: cvwl(230px) 0 cvwl(51px);
      font-size: cvwl(35px);
    }

    @include viewport--md {
      max-width: 40vw;
      font-size: cvwt(30px);
      margin: 140px 0 20px;
    }

    @include viewport--sm {
      text-align: center;
      line-height: 1.5;
      max-width: none;
      margin: 180px 0 30px;
      font-size: 29px;
    }
  }

  &-pros {
    display: flex;
    padding: cvw(20px) 0 cvw(20px) cvw(37px);
    border-left: solid 3px $dark-blue;

    @include viewport--lg {
      padding: cvwl(20px) 0 cvwl(20px) cvwl(15px);
    }

    @include viewport--md {
      flex-direction: column;
      padding: 10px 0 10px 10px;
    }

    @include viewport--sm {
      flex-direction: row;
      padding: 10px 0 10px 20px;
      margin-bottom: 20px;
    }

    @include viewport--xs {
      flex-direction: column;
      padding: 10px 0 10px 20px;
    }

    .item {
      @include viewport--sm {}

      &:nth-child(2) {
        margin: 0 cvw(30px);

        @include viewport--lg {
          margin: 0 cvwl(30px);
        }

        @include viewport--md {
          margin: 10px 0;
        }

        @include viewport--sm {
          margin: 0 15px;
        }

        @include viewport--xs {
          margin: 10px 0;
        }
      }

      h2 {
        font-size: 17px;
        line-height: 1.6;
        color: $dark-blue;
        text-transform: uppercase;
        margin: 0 0 cvw(20px);

        @include viewport--lg {
          margin: 0 0 cvwl(20px);
          font-size: 16px;
        }

        @include viewport--md {
          margin: 0 0 10px;

          br {
            display: none;
          }
        }

        @include viewport--sm {
          br {
            display: block;
          }
        }

        @include viewport--xs {
          br {
            display: none;
          }
        }
      }

      p {
        color: $gray-darken;
        margin-bottom: 0;

        span {
          color: $gray-blue;
        }
        font-size: 15px;
        line-height: 1.6;

        @include viewport--lg {
          font-size: 13.5px;
        }

        @include viewport--md {
          max-width: 310px;

          br {
            display: none;
          }
        }
      }
    }
  }

  .btn {
    padding: cvw(21px) cvw(118px) cvw(21px) cvw(62px);
    max-width: unset;
    font-size: 16px;
    margin: cvw(62px) 0;
    box-shadow: 7.5px 13px 34px 0 rgba(12, 30, 44, 0.2);

    @include viewport--lg {
      padding: cvwl(21px) cvwl(118px) cvwl(21px) cvwl(62px);
    }

    @include viewport--md {
      br {
        display: none;
      }
      font-size: 14px;
      max-width: 360px;
      padding: 15px 30px 15px 30px;
    }

    @include viewport--sm {
      font-size: 13px;
      max-width: unset;
      width: 100%;
      padding-right: 80px;
      margin: 20px 0;
    }
  }

  &-pluses {
    margin-bottom: cvw(120px);

    @include viewport--lg {
      margin-bottom: cvwl(120px);
    }

    @include viewport--md {
      margin-bottom: 0;
    }

    .item {
      font-size: cvw(23px);
      line-height: 2;
      color: $dark-blue;
      display: flex;
      align-items: center;
      margin-bottom: 0;

      @include viewport--lg {
        font-size: cvwl(23px);
      }

      @include viewport--md {
        font-size: 16px;
      }

      span {
        display: flex;
        width: 30px;
        height: 30px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-left: 15px;
        background: #f0d276;
      }
    }
  }
}
