.feature-list {
  @include list-reset;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-column-gap: 22px;

  @include viewport--lg {
    grid-column-gap: 15px;
  }

  @include viewport--md {
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 20px;
  }

  @include viewport--xs {
    grid-template-columns: 1fr;
    grid-gap: 20px;
  }

  &__item {
    background-color: $white;
    color: $dark-blue;
    box-shadow: 7.5px 13px 34px 0 rgba(12, 30, 44, 0.2);
    padding: 8px;

    &-wrap {
      border: solid 1px rgba(240, 210, 118, 0.19);
      text-align: center;
      padding: 54px 0 44px;
      height: 100%;

      @include viewport--lg {
        padding: 20px;
      }

      @include viewport--md {
        padding: 30px;
      }
    }
  }

  &__title {
    margin: 0 0 20px;
    font-family: "Cormorant", serif;
    font-size: 42px;
    font-weight: 600;
    font-stretch: normal;
    font-style: normal;
    line-height: 1;
    color: #7e96aa;

    @include viewport--lg {
      font-size: 26px;
    }
  }

  &__dsc {
    font-size: 18px;
    margin: 0;
    line-height: normal;
    color: $dark-blue-saturate;

    @include viewport--lg {
      font-size: 16px;

      br {
        display: none;
      }
    }
  }
}
