.estate {
  background: url("../../img/bg/estate-bg.jpg") no-repeat center;
  background-size: cover;
  padding: 230px 40px 0;

  @include viewport--lg {
    padding: 160px 40px 0;
  }

  @include viewport--md {
    background: url("../../img/bg/estate-tablet-bg.jpg") no-repeat center;
    background-size: cover;
  }

  @include viewport--sm {
    padding: 180px 15px 0;
  }

  &__text {
    max-width: 1420px;
    padding: 0 40px;

    @include viewport--lg {
      padding: 0;
    }
    width: 100%;
    margin: 0 auto;

    &-comment {
      display: flex;
      align-items: center;
      max-width: 570px;
      font-size: 20px;
      line-height: 1.5;

      @include viewport--lg {
        font-size: 16px;
      }

      @include viewport--sm {
        svg {
          width: 30px;
          min-width: 30px;
        }
        align-items: flex-start;

        br {
          display: none;
        }
      }

      p {
        margin: 0 0 0 18px;
      }
    }

    &-pluses {
      margin-bottom: 56px;

      .item {
        display: flex;
        align-items: baseline;

        @include viewport--lg {
          align-items: center;
        }

        @include viewport--sm {
          align-items: baseline;
        }

        p {
          margin-bottom: 0;
        }

        &:first-child {
          margin-bottom: 16px;
        }
      }

      p {
        font-size: 23px;

        @include viewport--lg {
          font-size: 18px;
        }

        @include viewport--sm {
          max-width: 380px;

          br {
            display: none;
            font-size: 16px;
          }
        }

        @include viewport--sm {
          font-size: 16px;
        }
      }

      .plus {
        width: 30px;
        height: 30px;
        min-width: 30px;
        min-height: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 23px;
        color: $dark-blue;
        background: $white;
        border-radius: 50%;
        margin-right: 20px;
      }
    }

    .btn {
      margin: 29px 0 66px;
      padding: 32px 113px 32px 46px;
      max-width: unset;
      font-size: 18px;
      line-height: 1.5;

      @include viewport--lg {
        font-size: 16px;
        max-width: 350px;
        padding: 20px 90px 20px 46px;
        margin: 25px 0 50px;
      }

      @include viewport--sm {
        width: 100%;
        max-width: unset;
        padding: 15px 80px 15px 15px;
        font-size: 16px;
      }

      span {
        width: 77px;
      }
    }
  }

  &__title {
    font-family: "Cormorant", serif;
    font-size: 80px;
    font-weight: 600;
    font-stretch: normal;
    font-style: normal;
    line-height: 1;
    letter-spacing: normal;
    text-align: left;
    color: #141414;
    max-width: 800px;
    margin: 0 0 62px;

    @include viewport--lg {
      font-size: 56px;
      max-width: 600px;
      margin: 0 0 50px;
    }

    @include viewport--xs {
      font-size: 32px;
      margin: 0 0 30px;
    }
  }
}
