.ts-panel {
  display: none;
}

.footer {
  padding: 80px 40px 60px;
  display: flex;
  justify-content: space-between;

  @include viewport--xl {
    flex-wrap: wrap;
    padding: 60px 40px 30px;
  }

  @include viewport--sm {
    padding: 20px 15px 30px;
  }

  @include viewport--xs {
    justify-content: center;
  }

  &__logo img {
    @include viewport--sm {
      min-width: 200px;
      max-width: 200px;
    }

    @include viewport--xs {
      margin: 0 auto;
    }
  }

  &-company {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @include viewport--xl {
      width: 23%;
      order: 1;
      min-width: 250px;
    }

    @include viewport--md {
      width: 100%;
      order: 1;
      min-width: unset;
      flex-direction: row;
      justify-content: flex-start;
      margin-bottom: 20px;
    }

    @include viewport--xs {
      flex-direction: column;
    }

    p {
      margin: 37px 0 0;
      //FIXME: добавить веб-безопасный шрифт
      font-family: "Avenir";
      font-size: 16.7px;
      font-weight: normal;
      line-height: 1.5;
      letter-spacing: 0.33px;

      @include viewport--md {
        margin: 0 0 0 25px;
      }

      @include viewport--sm {
        br {
          display: none;
        }
      }

      @include viewport--xs {
        margin: 15px auto 0;
        max-width: 290px;
        text-align: center;
      }
    }
  }

  &-requisites {
    //FIXME: добавить веб-безопасный шрифт
    font-family: "Avenir";
    font-size: 16.7px;
    font-weight: normal;
    line-height: 1.5;
    letter-spacing: 0.33px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @include viewport--xl {
      flex-wrap: wrap;
      width: 23%;
      order: 3;
      min-width: 270px;
      align-items: flex-end;
      text-align: right;
    }

    @include viewport--md {
      order: 2;
      align-items: flex-start;
      text-align: left;
    }

    @include viewport--sm {
      font-size: 14px;
      width: auto;
      min-width: 0;
      flex-shrink: 1;
      flex-grow: 0;
      flex-basis: auto;
    }

    @include viewport--xs {
      justify-content: center;
    }

    p {
      color: #000000;
      margin: 0;
    }

    a {
      color: #898989;
      text-decoration: none;
    }
  }

  &-contact {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @include viewport--xl {
      flex-wrap: wrap;
      width: 30%;
      min-width: 400px;
      order: 2;
      justify-content: center;
    }

    @include viewport--md {
      order: 3;
      align-items: flex-end;
      text-align: right;
    }

    @include viewport--sm {
      align-items: center;
      min-width: 0;
      flex-grow: 1;
      // width: 100%;
      // align-items: flex-start;
      // text-align: left;
      // margin-top: 20px;
    }

    @include viewport--xs {
      padding-top: 1em;
      width: 100%;
      align-items: center;
      text-align: center;
      min-width: unset;
    }

    .social {
      margin-bottom: 25px;

      @include viewport--xl {
        justify-content: center;
        margin-bottom: 15px;
      }
    }

    .info {
      font-size: 16.7px;
      font-weight: normal;
      line-height: 1.68;
      letter-spacing: normal;
      text-align: left;
      color: #000000;

      @include viewport--xl {
        text-align: center;
      }

      @include viewport--md {
        text-align: right;
      }

      @include viewport--sm {
        text-align: center;
        font-size: 14px;
        width: 100%;
      }

      span {
        font-weight: 700;

        &.icon-wrap {
          display: inline-block;
          width: 20px;
          margin-right: 15px;
        }
      }

      a {
        color: #000000;
        text-decoration: none;
      }

      p {
        margin: 0;
      }
    }
  }

  &-designer {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @include viewport--xl {
      flex-wrap: wrap;
      width: 50%;
      order: 4;
      margin-top: 20px;
    }

    @include viewport--xs {
      order: 5;
      width: 100%;
    }

    &__wrap {
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      @include viewport--xs {
        align-items: center;
      }

      span {
        font-size: 14px;
      }
    }
  }

  &-copywrite {
    font-size: 14px;
    font-weight: normal;
    line-height: 1.79;
    letter-spacing: 0.28px;
    color: rgba(137, 137, 137, 0.5);
    margin: 0;

    @include viewport--xs {
      text-align: center;
      margin-top: 15px;
    }
  }

  &-call {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;

    @include viewport--xl {
      flex-wrap: wrap;
      width: 50%;
      order: 5;
      margin-top: 20px;
    }

    @include viewport--xs {
      order: 4;
      width: 100%;
    }

    .btn {
      background-color: $dark-blue;
      color: $white;

      @include viewport--xl {
        margin-bottom: 15px;
      }

      @include viewport--xs {
        padding: 13.5px 20px;
        margin-bottom: 0;
      }
    }

    a {
      font-size: 14px;
      font-weight: normal;
      line-height: 1.79;
      letter-spacing: 0.28px;
      color: rgba(137, 137, 137, 0.5);
      text-decoration: none;

      @include viewport--xs {
        display: none;
      }
    }
  }
}
