.social {
    display: flex;
    @include viewport--lg {
        margin-right: 10px;
    }
    @include viewport--md {
        margin-right: 0px;
    }
    &__text {
        font-size: 14px;
        width: 120px;
        margin-right: 16px;
        @include viewport--lg {
            display: none;
        }
    }
    &__text span {
        color: $green;
        font-weight: bold;
    }
    &__list {
        @include list-reset;
        display: flex;
    }
    &__item {
        &:nth-child(2) {
            margin: 0 7px;
        }
    }
    &__link {
        width: 44px;
        height: 44px;
        font-size: 20px;
        color: $white;
        display: flex;
        align-items: center;
        justify-content: center;
        text-decoration: none;
        border-radius: 50%;
        &.viber {
            background-color: #7660f3;
        }
        &.telegram {
            background-color: #09a2f0;
        }
        &.whatsapp {
            background-color: #2fcf6b;
        }
    }
}