.header {
    display: flex;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    width: 100%;
    padding: 45px 40px 0;
    @include viewport--lg {
        padding: 45px 15px 0;
    }
    &__logo {
        img {
            @include viewport--lg {
                width: 180px;
            }
            @include viewport--sm {
                width: 211px;
                margin-bottom: 15px;
            }
        }
    }
    &__house {
        background: $dark-blue;
        color: $white;
        .btn {
            &-first {
                margin-right: 24px;
            }
            @include viewport--md {
                display: none;
            }
        }
        .whatsapp {
            @include viewport--lg {
                display: none;
            }
        }
    }
    &__estate {
        .btn {
            padding-right: 90px;
            @include viewport--lg {
                max-width: 240px;
                padding-right: 60px;
            }
        }
    }
    &__interior {
        .header__wrapper {
            justify-content: unset;
            @include viewport--lg {
                justify-content: space-between;
            }
        }
        .header__text {
            max-width: 150px;
            margin: 0 7vw 0 3.6vw;
            @include viewport--lg {
                margin: 0 10px;
            }
        }
        .social__text {
            text-align: right;
        }
        .social {
            margin-right: auto;
            @include viewport--lg {
                margin-right: unset;
            }
        }
        .header__col {
            .phone {
                color: $white;
                @include viewport--lg {
                    color: #000;
                }
                @include viewport--md {
                    color: $white;
                }
                @include viewport--sm {
                    color: #000;
                }
            }
        }
        .btn {
            padding-right: 80px;
            @include viewport--lg {
                padding-right: 35px;
                max-width: 240px;
            }
        }
    }
    &__wrapper {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        @include viewport--sm {
            flex-direction: column;
        }
    }
    &__text {
        max-width: 280px;
        font-size: 14px;
        @include viewport--lg {
            max-width: 200px;
            margin: 0 10px;

            br {
              display: none;
            }
        }
        @include viewport--md {
            display: none;
        }
    }
    &__phone {
        vertical-align: middle;
        padding-right: 2em;
        @include viewport--lg {
            padding-right: 1em;
        }
    }
    &__btn {
        vertical-align: middle;
        @include viewport--md {
            display: none;
        }
    }
    p {
        margin-bottom: 0;
    }
    &__col {
        @include viewport--lg {
            display: flex;
            align-items: center;
        }
        &--fit-content {
            @include viewport--md {
                display: none;
            }
        }
        &.social {
            @include viewport--sm {
                display: none;
            }
        }
    }
}
