.fs {
  // padding-top: 2%;
  display: grid;
  grid-template-columns: 168px 450px 200px 1fr;
  grid-auto-rows: min-content;
  // grid-template-rows: repeat(3, auto);
  background-color: $dark-blue;
  color: $white;
  // max-height: 100vh;
  overflow: hidden;

  &--white-bg {
    color: #0a0b0b;
    background-color: $white;
  }
}

.fs__header {
  grid-column: 1 / -1;
  grid-row: 1 / 2;
}

.fs__text {
  grid-column: 2 / 4;
  grid-row: 2 / 3;
  z-index: 1;
  align-self: center;
}

.fs__slider {
  grid-column: 3 / 5;
  grid-row: 2 / 4;
}

.fs__footer {
  grid-column: 1 / -1;
  grid-row: 3 / 4;
}

.fs__img {
  width: 100%;
  object-fit: cover;
}
