.phone {
    margin-bottom: 0;
    display: inline-flex;
    flex-direction: column;
    font-size: 14px;
    text-align: right;
    &__text::before {
        content: "";
        display: inline-flex;
        margin-right: 10px;
        width: 8px;
        height: 8px;
        background-color: $color-main-dark;
        border-radius: 50%;
    }
    &__text--green::before {
        background-color: $green;
    }
    &__link {
        font-size: 21px;
        font-weight: bold;
    }
}