@mixin font($font_name, $file_name, $weight, $style) {
  @font-face {
    font-style: #{$style};
    font-weight: #{$weight};
    font-family: $font_name;
    font-display: swap;
    src: local("#{$file_name}"),
      url("../fonts/#{$file_name}.woff") format("woff"),
      url("../fonts/#{$file_name}.woff2") format("woff2");
  }
}

@mixin vh100 {
  min-height: 100vh;
  min-height: calc(var(--vh, 1vh) * 100); // stylelint-disable-line
}

@mixin hide-text {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@mixin list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      @content;
  }
}

/* ======================================
   $CVW
   ====================================== */

@function cvw($width) {
  @return 100vw * ($width / 1920px);
}

@function cvwl($width) {
  @return 100vw * ($width / 1440px);
}

@function cvwt($width) {
  @return 100vw * ($width / 1024px);
}

@function cvm($width) {
  @return 100vw * ($width / 767px);
}


/* ======================================
   $Desktop first
   ====================================== */

// < 1920
@mixin viewport--xl {
  @media (max-width: $viewport--xxl - 0.02) {
    @content;
  }
}
// < 1440
@mixin viewport--lg {
  @media (max-width: $viewport--xl - 0.02) {
    @content;
  }
}
// < 1024
@mixin viewport--md {
  @media (max-width: $viewport--lg - 0.02) {
    @content;
  }
}
// < 768
@mixin viewport--sm {
  @media (max-width: $viewport--md - 0.02) {
    @content;
  }
}
// < 480
@mixin viewport--xs {
  @media (max-width: $viewport--sm - 0.02) {
    @content;
  }
}

