.house {
  padding-top: 161px;
  background: $dark-blue;
  display: flex;
  justify-content: space-between;
  position: relative;
  @include viewport--md {
    flex-direction: column;
  }
  &__title {
    font-family: 'Commissioner';
    font-size: cvw(54px);
    font-weight: 300;
    line-height: normal;
    color: $white;
    margin: cvw(46px) 0 cvw(97px);
    @include viewport--lg {
      font-size: cvwl(48px);
      margin: cvwl(35px) 0 cvwl(40px);
    }
    @include viewport--md {
      font-size: 42px;
      margin: 35px 0 40px
    }
    @include viewport--sm {
      font-size: 36px;
      margin: 25px 0 35px
    }
    span {
      font-weight: 700;
    }
    &-before {
      margin-top: cvw(46px);
      font-size: 16px;
      font-weight: normal;
      line-height: 1.31;
      color: $gray-blue;
      @include viewport--lg {
        margin-top: cvwl(0px);
      }
      @include viewport--sm {
        margin-top: 20px;
      }
    }
  }
  &__chat {
    display: flex;
    font-size: cvw(20px);
    font-weight: normal;
    line-height: 1.2;
    letter-spacing: normal;
    color: $white;
    @include viewport--lg {
      display: none;
    }
    svg {
      margin-right: 20px;
    }
    p {
      max-width: cvw(280px);
      margin: 0 0 cvw(39px);
    }
  }
  &__btn {
    // padding: cvw(35px) cvw(125px) cvw(33px) cvw(54px);
    padding: cvwl(20px) cvwl(110px) cvwl(20px) cvwl(45px);
    // @include viewport--lg {
    //   padding: cvwl(20px) cvwl(110px) cvwl(20px) cvwl(45px);
    // }
    @include viewport--md {
      padding: 20px 80px 20px 30px;
      width: 440px;
      max-width: unset;
      margin-bottom: 40px;
      br {
        display: none;
      }
    }
    @include viewport--sm {
      font-size: 13px;
      padding: 20px 110px 20px 30px;
    }
  }
  &__text {
    &-wrap {
      position: absolute;
      top: 50%;
      left: cvw(180px);
      transform: translateY(-50%);
      max-width: cvw(640px);
      z-index: 99;
      @include viewport--lg {
        left: cvwl(100px);
      }
      @include viewport--md {
        position: relative;
        transform: unset;
        top: unset;
        left: unset;
        max-width: unset;
        width: 100%;
        padding: 0 40px;
      }
      @include viewport--md {
        padding: 0 15px;
      }
    }
  }
  &__slider {
    width: cvw(1245px);
    min-width: cvw(1245px);
    overflow: hidden;
    max-height: cvw(939px);
    position: relative;
    @include viewport--lg {
      width: cvwl(900px);
      min-width: cvwl(900px);
      max-height: cvwl(681px);
    }
    @include viewport--md {
      width: 100%;
      max-height: calc(100vw / 1.32);
      min-width: unset;
    }
    .swiper-button-next::after,
    .swiper-button-prev::after {
      content: "";
    }
    .swiper-button-next {
      right: 40px;
      bottom: 112px;
      left: unset;
      top: unset;
      width: 50px;
      height: 50px;
      @include viewport--lg {
        bottom: cvwl(360px);
        width: cvwl(50px);
        height: cvwl(50px);
      }
      @include viewport--md {
        bottom: 100px;
        width: 40px;
        height: 40px;
        right: 20px;
      }
      @include viewport--sm {
        bottom: 80px;
        width: 30px;
        height: 30px;
      }
    }
    .swiper-button-prev {
      right: 40px;
      bottom: 44px;
      width: 50px;
      height: 50px;
      left: unset;
      top: unset;
      @include viewport--lg {
        bottom: cvwl(290px);
        width: cvwl(50px);
        height: cvwl(50px);
      }
      @include viewport--md {
        bottom: 44px;
        width: 40px;
        height: 40px;
        right: 20px;
      }
      @include viewport--sm {
        width: 30px;
        height: 30px;
      }
    }
    .item {
      margin-left: auto;
      &-content {
        position: relative;
      }
      ul {
        position: absolute;
        bottom: 40px;
        left: 10px;
        display: flex;
        justify-content: space-evenly;
        margin: 0;
        padding: 0;
        right: 90px;

        @include viewport--lg {
          left: 20px;
          right: 0;
          align-items: flex-start;
        }

        @include viewport--md {
          right: 60px;
        }

        @include viewport--sm {
          flex-direction: column;
          bottom: 0;
          left: 0;
          right: 0;
          padding: 30px 15px;
          background: rgba(0, 0, 0, .5);
          align-items: initial;
        }

        @include viewport--sm {
          top: 0;
          padding: 10px 15px;
        }

        li {
          max-width: 23%;
          @include viewport--sm {
            display: flex;
            max-width: none;
            margin-right: 40px;
          }
          .plus {
            font-size: 20px;
            line-height: 1;
            color: $dark-blue;
            width: 30px;
            height: 30px;
            display: flex;
            justify-content: center;
            align-items: center;
            background: $color-main;
            margin-bottom: 24px;
            @include viewport--sm {
              flex-shrink: 0;
              margin-right: 10px;
            }

          }
          p {
            font-size: cvw(20px);
            font-weight: normal;
            line-height: 1.2;
            color: $white;
            margin: 0;
            @include viewport--lg {
              font-size: 14px;
            }
            @include viewport--md {
              font-size: 16px;
            }
            @include viewport--xs {
              font-size: 13.5px;
            }
          }
          // &:nth-child(2) {
          //   margin: 0 cvw(60px);
          //   @include viewport--lg {
          //     margin: 0 cvwl(30px);
          //   }
          //   @include viewport--md {
          //     margin: 0 30px;
          //   }
          //   @include viewport--sm {
          //     margin: 15px 0;
          //   }
          // }
        }
      }
    }
  }
  &__gift {
    position: absolute;
    left: 40px;
    bottom: 40px;
    p {
      font-family: 'Commissioner';
      font-size: 20px;
      font-weight: normal;
      line-height: 1.2;
      letter-spacing: normal;
      color: #7d97aa;
      margin: 20px 0 0;
    }
  }
}
